export const markerUrls = {
    red: "https://img.icons8.com/color/344/marker--v1.png",
    blue: "https://img.icons8.com/ultraviolet/344/marker.png",
};
export const createText = () => {
    return new ol.style.Text({
        overflow: true,
        scale: 1.6,
        offsetY: -43,
        fill: new ol.style.Fill({
            color: "#fff",
        }),
        stroke: new ol.style.Stroke({
            color: "0",
            width: 2,
        }),
    });
};
export const createIcon = ({ color = "red", iconScale = 0.1, } = {}) => {
    return new ol.style.Icon({
        src: markerUrls[color],
        scale: iconScale,
        anchor: [0.5, 1],
    });
};
export const createStyle = ({ image, text }) => {
    return new ol.style.Style({
        image: image,
        text: text,
    });
};
/**
 * Creates ol source from given features
 * @param features
 * @returns ol source
 */
export const createSource = (features) => {
    return new ol.source.Vector({
        features,
    });
};
/**
 * Creates ol layer
 * @returns ol layer
 */
export const createLayer = ({ target = "points", style, source, }) => {
    return new ol.layer.Vector({
        target,
        source,
        style,
    });
};
/**
 * Changes points given from neshan server to openlayers-friendly points
 * @param items Items from search result
 * @param color color of the markers
 */
export const createMapPoints = (items, color = "blue") => {
    return items.map((item) => {
        const point = Object.values(item.location);
        const stdPoint = ol.proj.transform(point, "EPSG:4326", "EPSG:3857");
        return {
            coords: stdPoint,
            text: item.title,
            color: color,
            iconScale: 0.07,
        };
    });
};
